@import "../../global.scss";

.secondSlide {
  background-color: white;
  width: 100%;
  height: 100%;
  box-shadow: 15px -11px 15px 3px rgba(0, 0, 0, 0.55);
  display: flex;
  flex-direction: column;
  align-items: center;

  .mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    justify-content: center;

    @include laptopL{
      width: 80%;
    }

    h3 {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 15px 0 20px 0;

      @include laptopL{
        margin-top: 35px;
      }
    }

    .content {
      display: flex;
      text-align: justify;
      font-size: 18px;
      line-height: 1.6;

      @include laptop{
      width: 95%;
      font-size: 17px;
      }
    }
  }

  .ownName {
    display: flex;
    justify-content: center;
    font-family: "Carattere", cursive;
    font-size: 40px;
    margin-top: 10%;
  }
}
