@import "../../global.scss";

.firstSlide {
  background-color: white;
  width: 100%;
  height: 100%;
  box-shadow: 15px -11px 15px 3px rgba(0, 0, 0, 0.55);

  .mainContainer {
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 10px;

    .dividingLine {
      border-right: 3px solid black;
      border-image: linear-gradient(
          to bottom,
          rgb(180, 148, 4),
          rgba(253, 234, 128, 0)
        )
        1 50%;
    }

    .leftContainer {
      flex: 1;

      margin-right: 20px;
      display: flex;
      flex-direction: column;

      @include laptopL {
        margin-right: 15px;
      }

      @include laptop{
    margin-right: 10px;
      }

      .personalInfo {
        display: flex;
        flex-direction: column;

        h3 {
          display: flex;
          justify-content: flex-end;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }

        p {
          display: flex;
          margin: 5px 0 5px 0;
          align-items: center;
          justify-content: flex-end;

          @include laptopL {
            margin-bottom: 0;
            font-size: 14px;
          }

          .icons {
            margin-right: 5px;
          }
        }
      }

      .education {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        width: 100%;

        h3 {
          display: flex;
          justify-content: flex-end;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }

        h4 {
          display: flex;
          justify-content: flex-end;
          margin: 5px 0 5px 0;

          @include laptopL {
            font-size: 15px;
          }

          .icons {
            margin-right: 5px;
          }
        }

        p {
          display: flex;
          margin: 5px 0 5px 0;
          align-items: center;
          justify-content: flex-end;

          @include laptopL {
            font-size: 14px;
          }
        }
      }

      .courses {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        width: 100%;

        .courseName {
          text-decoration: none;
        }

        h3 {
          display: flex;
          justify-content: flex-end;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }

        p {
          display: flex;
          margin: 8px 0 5px 0;
          justify-content: flex-end;

          @include laptopL {
            font-size: 14px;
          }
        }
        ul {
          list-style-type: none;

          li {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin: 8px 0 8px 0;

            @include laptopL {
              font-size: 14px;
            }
          }
        }
      }
    }

    .rightContainer {
      flex: 1;
      margin-left: 20px;
      display: flex;
      flex-direction: column;

      @include laptopL {
        margin-left: 15px;
      }

      @include laptop{
    margin-left: 10px;
      }

      .techSkills {
        display: flex;
        flex-direction: column;

        h3 {
          display: flex;
          justify-content: flex-start;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }
        ul {
          list-style-type: none;
          padding-left: 0;

          li {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin: 5px 0 5px 0;

            @include laptopL {
              font-size: 14px;
            }

            .icons {
              margin-right: 5px;
            }
          }
        }
      }

      .qualities {
        display: flex;
        flex-direction: column;
        margin-top: 15px;

        h3 {
          display: flex;
          justify-content: flex-start;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }
        ul {
          list-style-type: none;
          padding-left: 0;

          li {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin: 5px 0 5px 0;

            @include laptopL {
              font-size: 14px;
            }
          }
        }
      }

      .languages {
        display: flex;
        flex-direction: column;
        margin-top: 15px;

        h3 {
          display: flex;
          justify-content: flex-start;
          margin-bottom: 5px;

          @include laptopL {
            font-size: 18px;
          }
        }
        ul {
          list-style-type: none;
          padding-left: 0;

          li {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin: 5px 0 5px 0;
           
            @include laptopL {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}
