@import "global.scss";

* {
  margin: 0;
  font-family: "Barlow", sans-serif;
  color: rgb(109, 93, 24);
}

html {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  background-image: url("./assets/backGroundImage.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.app {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 40%;
  height: 90%;
  box-shadow: 15px -11px 15px 3px rgba(0, 0, 0, 0.55);

  @include laptopL{
    width: 50%;
      }

  @include laptop{
    width: 60%;
  }
}

.BrainhubCarousel__arrows.BrainhubCarousel__arrowRight {
  background-color: cyan;
  position: absolute;
  right: 50px;
}

.BrainhubCarousel__arrows.BrainhubCarousel__arrowLeft {
  background-color: cyan;
  position: absolute;
  left: 50px;
}
