@import "../../global.scss";

.headerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px 0 30px 0;

  @include laptopL {
    margin: 0;
  }

  .headingContainer {
    border: 2px solid #947928;
    padding: 10px 70px;
    align-items: center;

    @include laptopL {
      font-size: 20px;
      padding: 5px 50px;
      margin-top: 0;
    }
  }

  .paragraphContainer {
    margin: 15px 0 15px 0;
  }

  .personalPhoto {
    border-radius: 109px 0px 109px 109px;
    height: 100px;
    width: 100px;
    object-fit: cover;
  }
}
